import { h } from 'preact';
import { Select } from '../components/select';
import { SelectMultiple } from '../components/select-multiple';
import { InputDate } from '../components/date';
import { Button } from '../components/button';
import { flex, flexCol, flexItem20 } from '../constants';
const dataSets = ['Active', 'Confirmed', 'Deaths', 'Recoveries'];
const modes = ['By date', 'By first confirmed', 'By first 100 confirmed'];
const scaleTypes = ['Linear', 'Logarithmic'];
export function LineGraphControls({ countries, currentSeries, onChange, reload, state, }) {
    function selectMode(mode) {
        this.props.onChange(Object.assign(Object.assign({}, this.props.state), { mode }));
    }
    function selectDate(ymdString) {
        this.props.onChange(Object.assign(Object.assign({}, this.props.state), { startDate: ymdString }));
    }
    function selectDataSets(dataSetIndexes) {
        this.props.onChange(Object.assign(Object.assign({}, this.props.state), { dataSetIndexes }));
    }
    function selectCountries(countryIndexes) {
        this.props.onChange(Object.assign(Object.assign({}, this.props.state), { countryIndexes }));
    }
    function selectScaleType(scaleType) {
        this.props.onChange(Object.assign(Object.assign({}, this.props.state), { scaleType }));
    }
    return (h("section", { className: `${flex} ${flexItem20}` },
        h("section", { className: flexCol },
            h(Select, { onChange: selectMode.bind(this), options: modes, selected: state.mode }),
            h(Select, { onChange: selectScaleType.bind(this), options: scaleTypes, selected: state.scaleType })),
        h(InputDate, { onChange: selectDate.bind(this), ymdString: state.startDate }),
        h(SelectMultiple, { onChange: selectDataSets.bind(this), options: dataSets, selected: state.dataSetIndexes }),
        h(SelectMultiple, { onChange: selectCountries.bind(this), options: countries, selected: state.countryIndexes }),
        h(Button, { classes: ['green'], label: "Reload", onClick: reload })));
}
